import React, { useState, useEffect } from 'react';
import {
  Paper,
  Typography,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Chip,
  Box,
  Tabs,
  Tab,
  Grid
} from '@mui/material';
import {
  Add,
  Delete,
  Edit,
  Email,
  Phone,
  Group,
  Upload
} from '@mui/icons-material';
import { toast } from 'react-toastify';

function RecipientManager() {
  const [recipients, setRecipients] = useState([]);
  const [groups, setGroups] = useState([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [currentRecipient, setCurrentRecipient] = useState({
    name: '',
    email: '',
    phone: '',
    groups: []
  });
  const [editingId, setEditingId] = useState(null);
  const [tabValue, setTabValue] = useState(0);
  const [newGroupName, setNewGroupName] = useState('');

  useEffect(() => {
    loadRecipients();
    loadGroups();
  }, []);

  const loadRecipients = () => {
    const savedRecipients = localStorage.getItem('recipients');
    if (savedRecipients) {
      setRecipients(JSON.parse(savedRecipients));
    }
  };

  const loadGroups = () => {
    const savedGroups = localStorage.getItem('groups');
    if (savedGroups) {
      setGroups(JSON.parse(savedGroups));
    } else {
      setGroups(['All', 'VIP', 'Marketing', 'Support']);
    }
  };

  const saveRecipients = (newRecipients) => {
    localStorage.setItem('recipients', JSON.stringify(newRecipients));
    setRecipients(newRecipients);
  };

  const handleOpenDialog = (recipient = null) => {
    if (recipient) {
      setCurrentRecipient(recipient);
      setEditingId(recipient.id);
    } else {
      setCurrentRecipient({
        name: '',
        email: '',
        phone: '',
        groups: []
      });
      setEditingId(null);
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setCurrentRecipient({
      name: '',
      email: '',
      phone: '',
      groups: []
    });
    setEditingId(null);
  };

  const handleSaveRecipient = () => {
    if (!currentRecipient.name || (!currentRecipient.email && !currentRecipient.phone)) {
      toast.error('Please provide name and at least email or phone');
      return;
    }

    let newRecipients;
    if (editingId) {
      newRecipients = recipients.map(r =>
        r.id === editingId ? { ...currentRecipient, id: editingId } : r
      );
    } else {
      newRecipients = [...recipients, {
        ...currentRecipient,
        id: Date.now().toString()
      }];
    }

    saveRecipients(newRecipients);
    toast.success(editingId ? 'Recipient updated' : 'Recipient added');
    handleCloseDialog();
  };

  const handleDeleteRecipient = (id) => {
    const newRecipients = recipients.filter(r => r.id !== id);
    saveRecipients(newRecipients);
    toast.success('Recipient deleted');
  };

  const handleImportCSV = (event) => {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const text = e.target.result;
        const lines = text.split('\n');
        const headers = lines[0].split(',').map(h => h.trim());

        const imported = lines.slice(1)
          .filter(line => line.trim())
          .map(line => {
            const values = line.split(',').map(v => v.trim());
            const recipient = {
              id: Date.now().toString() + Math.random(),
              groups: ['All']
            };

            headers.forEach((header, index) => {
              const value = values[index];
              if (header.toLowerCase().includes('name')) {
                recipient.name = value;
              } else if (header.toLowerCase().includes('email')) {
                recipient.email = value;
              } else if (header.toLowerCase().includes('phone')) {
                recipient.phone = value;
              }
            });

            return recipient;
          })
          .filter(r => r.name && (r.email || r.phone));

        const newRecipients = [...recipients, ...imported];
        saveRecipients(newRecipients);
        toast.success(`Imported ${imported.length} recipients`);
      } catch (error) {
        toast.error('Failed to import CSV file');
      }
    };

    reader.readAsText(file);
  };

  const handleAddGroup = () => {
    if (newGroupName && !groups.includes(newGroupName)) {
      const newGroups = [...groups, newGroupName];
      localStorage.setItem('groups', JSON.stringify(newGroups));
      setGroups(newGroups);
      setNewGroupName('');
      toast.success('Group added');
    }
  };

  const handleGroupToggle = (group) => {
    const currentGroups = currentRecipient.groups || [];
    const newGroups = currentGroups.includes(group)
      ? currentGroups.filter(g => g !== group)
      : [...currentGroups, group];

    setCurrentRecipient({ ...currentRecipient, groups: newGroups });
  };

  const exportRecipients = () => {
    const csv = [
      'Name,Email,Phone,Groups',
      ...recipients.map(r =>
        `${r.name},${r.email || ''},${r.phone || ''},"${(r.groups || []).join(', ')}"`
      )
    ].join('\n');

    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'recipients.csv';
    a.click();
    URL.revokeObjectURL(url);
    toast.success('Recipients exported');
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
        Recipient Manager
      </Typography>

      <Paper sx={{ mb: 3 }}>
        <Tabs value={tabValue} onChange={(e, v) => setTabValue(v)}>
          <Tab label="Individual Recipients" icon={<Email />} />
          <Tab label="Groups" icon={<Group />} />
        </Tabs>
      </Paper>

      {tabValue === 0 && (
        <>
          <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
            <Button
              variant="contained"
              startIcon={<Add />}
              onClick={() => handleOpenDialog()}
            >
              Add Recipient
            </Button>
            <Button
              variant="outlined"
              component="label"
              startIcon={<Upload />}
            >
              Import CSV
              <input
                type="file"
                accept=".csv"
                hidden
                onChange={handleImportCSV}
              />
            </Button>
            <Button
              variant="outlined"
              onClick={exportRecipients}
              disabled={recipients.length === 0}
            >
              Export CSV
            </Button>
          </Box>

          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell>Email</TableCell>
                  <TableCell>Phone</TableCell>
                  <TableCell>Groups</TableCell>
                  <TableCell align="right">Actions</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {recipients.map((recipient) => (
                  <TableRow key={recipient.id}>
                    <TableCell>{recipient.name}</TableCell>
                    <TableCell>
                      {recipient.email && (
                        <Chip
                          icon={<Email />}
                          label={recipient.email}
                          size="small"
                          variant="outlined"
                        />
                      )}
                    </TableCell>
                    <TableCell>
                      {recipient.phone && (
                        <Chip
                          icon={<Phone />}
                          label={recipient.phone}
                          size="small"
                          variant="outlined"
                        />
                      )}
                    </TableCell>
                    <TableCell>
                      <Box sx={{ display: 'flex', gap: 0.5, flexWrap: 'wrap' }}>
                        {(recipient.groups || []).map((group) => (
                          <Chip
                            key={group}
                            label={group}
                            size="small"
                            color="primary"
                          />
                        ))}
                      </Box>
                    </TableCell>
                    <TableCell align="right">
                      <IconButton onClick={() => handleOpenDialog(recipient)}>
                        <Edit />
                      </IconButton>
                      <IconButton onClick={() => handleDeleteRecipient(recipient.id)}>
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </>
      )}

      {tabValue === 1 && (
        <Box>
          <Grid container spacing={2} sx={{ mb: 3 }}>
            <Grid item xs={8}>
              <TextField
                fullWidth
                label="New Group Name"
                value={newGroupName}
                onChange={(e) => setNewGroupName(e.target.value)}
              />
            </Grid>
            <Grid item xs={4}>
              <Button
                fullWidth
                variant="contained"
                onClick={handleAddGroup}
                sx={{ height: '56px' }}
              >
                Add Group
              </Button>
            </Grid>
          </Grid>

          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" gutterBottom>
              Available Groups
            </Typography>
            <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
              {groups.map((group) => (
                <Chip
                  key={group}
                  label={`${group} (${recipients.filter(r => (r.groups || []).includes(group)).length} recipients)`}
                  color="primary"
                  variant="outlined"
                />
              ))}
            </Box>
          </Paper>
        </Box>
      )}

      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle>
          {editingId ? 'Edit Recipient' : 'Add Recipient'}
        </DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            label="Name"
            value={currentRecipient.name}
            onChange={(e) => setCurrentRecipient({ ...currentRecipient, name: e.target.value })}
            margin="normal"
          />
          <TextField
            fullWidth
            label="Email"
            type="email"
            value={currentRecipient.email}
            onChange={(e) => setCurrentRecipient({ ...currentRecipient, email: e.target.value })}
            margin="normal"
          />
          <TextField
            fullWidth
            label="Phone"
            value={currentRecipient.phone}
            onChange={(e) => setCurrentRecipient({ ...currentRecipient, phone: e.target.value })}
            margin="normal"
          />
          <Box sx={{ mt: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              Groups
            </Typography>
            <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
              {groups.map((group) => (
                <Chip
                  key={group}
                  label={group}
                  onClick={() => handleGroupToggle(group)}
                  color={(currentRecipient.groups || []).includes(group) ? 'primary' : 'default'}
                  variant={(currentRecipient.groups || []).includes(group) ? 'filled' : 'outlined'}
                />
              ))}
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button onClick={handleSaveRecipient} variant="contained">
            {editingId ? 'Update' : 'Add'}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default RecipientManager;